.card__main {
  position: absolute;
  height: calc(100vh - 64px);
  width: 30%;
  left: 33%;
  display: inline-block;
  margin: 0
} 
.card__img {
  max-height: 50vh;
  max-width: 90%;
} 
.card__action {
   justify-Content: center;
   display: flex;
   align-items: center;
   padding: 8px;
   bottom: 0;
   width: 100%;
   box-sizing: border-box;
   height: 8vh;
}
.card__text__p {
   overflow: auto;
   width: 100%;
   height: 10vmin;
   box-sizing: border-box;
   text-align: center;
}
.card__text__h3 {
  text-align: center
}
.sorting__parent {
  position: absolute;
  display: flex;
  width: 34%;
  left: 65%;
  height: calc(100vh - 64px);
  flex-direction: column;
  justify-content: space-around;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 10px, rgba(0, 0, 0, 0.23) 0px 3px 10px;
}
.menu__bar {
  text-align: center;
  position: relative;
}
